/* eslint-disable jsx-a11y/anchor-is-valid,jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions */
import React, { PureComponent } from 'react';
import { func } from 'prop-types';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from 'components/WithStyle';
import { push } from 'middleware/location';
import Layout from 'components/pz/PzLayout';
import style from './Thanks.css';
import { LOGOUT, MESSAGE_FINISH } from './constant';

class ThankYou extends PureComponent {
  static propTypes = {
    navigateLogin: func.isRequired,
  };

  static defaultProps = {};

  handleClick = () => {
    this.props.navigateLogin();
  };

  render() {
    return (
      <Layout className={style.thanksWrapper} hideBackButton hideNextButton>
        <p className={style.thanksDescription}>{MESSAGE_FINISH}</p>
        <a className={style.thanksLogout} onClick={this.handleClick}>
          {LOGOUT}
        </a>
        <img
          className={style.thanksImage}
          alt="thank you"
          src="/images/bg.png"
        />
      </Layout>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  navigateLogin: () => {
    dispatch(push('/login'));
  },
});

const enhance = compose(
  connect(
    null,
    mapDispatchToProps,
  ),
  withStyles(style),
);

export default enhance(ThankYou);
